
import styles from '../Modal.module.scss';
import  {AiFillCloseCircle} from 'react-icons/ai'
import React from 'react';
import {ModalPropsTestEdit } from '../../../default/interfaces/Interfaces';
import { Title } from 'src/components/Title/Title';
import { UserContext } from 'src/context/UserContext';
import { useContext, useEffect } from 'react';
import SearchCep from 'src/helpers/SearchCep';
import {TextField} from '@mui/material';
import { LoadingButton } from '@mui/lab';
import http from 'src/http';
import { IClient } from 'src/models/users';



export const ClientsModal = ({openModal, setModal, data, edit}: ModalPropsTestEdit) => {


  /*CLIENTES DETAILS*/

  //razão social
  const [editedRazaoSocial, setEditedRazaoSocial] = React.useState<string>(data?.entidade?.razao_social);
  const [newRazaoSocial, setNewRazaoSocial] = React.useState<string>('');

  //nome fantasia
  const [editedFantasyName, setEditedFantasyName] = React.useState<string>(data?.entidade?.nome_fantasia)
  const [newFantasyName, setNewFantasyName] = React.useState<string>('');

  //cnpj
  const [editedCnpj, setEditedCnpj] = React.useState<string>(data?.entidade?.documento);
  const [newCnpj, setNewCnpj] = React.useState<string>('');

  //email
  const [editedEmail, setEditedEmail] = React.useState<string>(data?.entidade?.email);
  const [newEmail, setNewEmail] = React.useState<string>('');

  //email
  const [editedPassword, setEditedPassword] = React.useState<string>(data?.entidade?.email);
  const [newPassword, setNewPassword] = React.useState<string>('');

  //id cliente
  const [idClient, setIdClient] = React.useState<string>(data?.entidade?.id);




  /*PAY ADDRESS*/

  //street
  const [payEditedStreet, setPayEditedStreet] = React.useState<string>(data?.entidade?.enderecos[0]?.endereco?.rua);
  const [payNewStreet, setPayNewStreet] = React.useState<string>('');


  //neighbourhood
  const [payEditedNeighbourhood, setPayEditedNeighbourhood] = React.useState<string>(data?.entidade?.enderecos[0]?.endereco?.bairro);
  const [payNewNeighbourhood, setPayNewNeighbourhood] = React.useState<string>('');

  //city
  const [payEditedCity, setPayEditedCity] = React.useState<string>(data?.entidade?.enderecos[0]?.endereco?.cidade?.nome);
  const [payNewCity, setPayNewCity] = React.useState<string>('');

  //state
  const [payEditedState, setPayEditedState] = React.useState<string>(data?.entidade?.enderecos[0]?.endereco?.cidade?.estado?.sigla);
  const [payNewState, setPayNewState] = React.useState<string>('');

  //number of address
  const [payEditedNumberAddress, setPayEditedNumberAddress] = React.useState(data?.entidade?.enderecos[0]?.endereco?.numero);
  const [payNewNumberAddress, setPayNewNumberAddress] = React.useState(null);

  //complement
  const [payEditedComplement, setPayEditedComplement] = React.useState<string>('');
  const [payNewComplement, setPayNewComplement] = React.useState<string>('');

  //cep
  const [payEditedCep, setPayEditedCep] = React.useState(data?.entidade?.enderecos[0]?.endereco?.cep);
  const [payNewCep, setPayNewCep] = React.useState('');

  //nome responsável
  const [nameEditedResp, setNameEditedResp] = React.useState<string>('');
  const [nameNewResp, setNameNewResp] = React.useState<string>('');



  const [loading, setLoading] = React.useState<Boolean>(false);
  const [success, setSuccess] = React.useState<string>('');
  const [error, setError] = React.useState<string>('');


  async function buscaCepBilling(cep: string) {
    const json = await SearchCep(cep);
    if(json){
      setPayNewStreet(json.logradouro);
      setPayNewCity(json.localidade);
      setPayNewState(json.uf)
      setPayNewNeighbourhood(json.bairro)
    }else{
      setPayNewStreet('');
      setPayNewCity('');
      setPayNewState('')
      setPayNewNeighbourhood('')
    }
  }



  async function editClientById(event: React.FormEvent<HTMLFormElement>){
    event.preventDefault();
    setLoading(true);
    http.put<IClient>(`/usuarios/${idClient}`, {
      "documento": editedCnpj,
      "email": editedEmail,
      "nome_fantasia": editedFantasyName,
      "razao_social": editedRazaoSocial,
      "tipo": "J"
    })
    .then(response=>{
      if(response.status == 202){
        setSuccess("Cliente editado com sucesso.");
        setError("")
        window.location.reload();
      }
    }).catch(error=>{
      if(error?.response?.data?.code == "ER_DUP_ENTRY"){
        setError("CNPJ já utilizado por outra conta.");
        setSuccess("");
      }
      else{
        setError("Erro no servidor, por favor, tente novamente.");
         setSuccess("");
      }
     
    }
    
    )
    setLoading(false);
  }

 
  
  async function createClient(event: React.FormEvent<HTMLFormElement>){
    event.preventDefault();
    setLoading(true);
    http.post('/usuarios', {
      "documento": newCnpj,
      "email": newEmail,
      "nome_fantasia": newFantasyName,
      "razao_social": newRazaoSocial,
      "tipo": "J",
      "id_perfil": 2,
      "login": newEmail,
    })
    .then(response=>{

        setSuccess("Cliente cadastrado com sucesso.");
        criar_endereco_cobranca(response?.data?.entidade?.id);
        setError('');
        window.location.reload();
      
    }).catch(error=>{
      if(error.response.data.errors[0].rule === 'unique' && error.response.data.errors[0].field === 'email'){
        setError("Email já cadastrado no sistema.");
        setSuccess("");
      }else if(error.response.data.errors[0].rule === 'unique' && error.response.data.errors[0].field === 'documento'){
        setError("CNPJ já cadastrado no sistema.");
        setSuccess("");
      }
    })
    setLoading(false);
  }

  function formatCnpjCpf(value: string){
    const cnpjCpf = value.replace(/\D/g, '');
    if (cnpjCpf.length === 14) {
      setNewCnpj(cnpjCpf.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/g, "\$1.\$2.\$3/\$4-\$5"));
    }    
  }

  async function buscar_cidade(nome_cidade: string) {
    const nome = encodeURI(nome_cidade);
    const { data } = await http.get(`/cidades/${nome}`);
    return data?.id;
  } 


  async function criar_endereco_cobranca(id_entidade: number) {
    http.post(`/enderecos/${id_entidade}`, {
        "id_cidade": await buscar_cidade(payNewCity),
        "cep": payNewCep,
        "bairro": payNewNeighbourhood,
        "complemento": payNewComplement? payNewComplement : 'Não possui',
        "rua": payNewStreet,
        "numero": payNewNumberAddress,
        "contato": "",
        "telefone": "",
        "email": "",
        "nome_obra": "",
        tipo: "C"
    })
    .then(response=>{
      //console.log(response);
    })
    .catch(error=>{
      console.log(error)
    })
  }

  const {
    usuario
  } = useContext(UserContext);

  useEffect(()=>{
    buscaCepBilling(payNewCep)
  }, [payNewCep])

  useEffect(()=>{
    formatCnpjCpf(newCnpj)
  }, [newCnpj])

  return (
    <>
      {openModal && (
        <div className={styles.modal}>
          <div className={styles.modal__divCloseButton}>
          <button 
              className={styles.modal__divCloseButton__closeButton}
              onClick={(e)=>setModal(!openModal)}
            >
              <AiFillCloseCircle/>
            </button>
          </div>

          <section>

            {usuario?.id_perfil === 1 ? 
              <Title>
                Editar cliente
              </Title>
            :
              <Title>
                Detalhes do cliente
              </Title>
            }            
              <form onSubmit={edit? editClientById : createClient}>
                <div style={{display: 'flex', justifyContent: 'space-between', gridGap: '2rem', width: "100%", alignItems: "center", marginTop: '1rem'}}>
                  <div style={{width: "100%"}}>
                    <TextField 
                      id="razao_social"
                      type="text"
                      label="Razão social"
                      fullWidth={true}
                      size="small"
                      required={true}
                      color="primary"
                      placeholder="Digite a razão social"
                      value={edit? editedRazaoSocial: newRazaoSocial}
                      onChange={
                        edit? (e)=>setEditedRazaoSocial(e.target.value): (e)=>setNewRazaoSocial(e.target.value)}
                    />
                  </div>
                  <div style={{width: "100%"}}>
                      <TextField 
                        id="fantasy_name"
                        type="text"
                        label="Nome fantasia"
                        fullWidth={true}
                        size="small"
                        required={true}
                        color="primary"
                        placeholder="Digite o nome fantasia"
                        value={edit? editedFantasyName: newFantasyName}
                        onChange={
                          edit? (e)=>setEditedFantasyName(e.target.value): (e)=>setNewFantasyName(e.target.value)}
                      />
                  </div>
                </div>

                <div style={{display: 'flex', justifyContent: 'space-between', gridGap: '2rem', width: "100%", alignItems: "center"}}>
                    <div style={{marginTop: '1rem', width: "100%"}}>
                      <TextField 
                          id="email_client"
                          type="text"
                          label="Email"
                          fullWidth={true}
                          size="small"
                          required={true}
                          color="primary"
                          placeholder="Digite o e-mail"
                          value={edit? editedEmail: newEmail}
                          onChange={
                          edit? (e)=>setEditedEmail(e.target.value): (e)=>setNewEmail(e.target.value)
                          }
                          onBlur={
                            edit? (e)=>setEditedEmail(e.target.value): (e)=>setNewEmail(e.target.value)
                          }
                          disabled={usuario?.id_perfil === 3 ? true : false}
                      />     
                    </div>

                    <div style={{marginTop: '1rem', width: "100%"}}>
                      <TextField 
                          id="cnpj_client"
                          type="text"
                          label="CNPJ"
                          fullWidth={true}
                          size="small"
                          required={true}
                          color="primary"
                          placeholder="Digite o CNPJ"
                          value={edit? editedCnpj: newCnpj}
                          onChange={
                            edit? (e)=>setEditedCnpj(e.target.value): (e)=>setNewCnpj(e.target.value)
                          }
                          onBlur={
                            edit? (e)=>setEditedCnpj(e.target.value): (e)=>setNewCnpj(e.target.value)
                          }
                          disabled={usuario?.id_perfil === 3 ? true : false}
                      />
                    </div>
                </div>


                <div className={styles.formCheckout__div} style={{marginTop: '2rem'}} >
                    <Title level={3}>
                        Detalhes do faturamento
                    </Title>

                    <div style={{display: "flex", justifyContent: "space-between", gridGap: "1rem"}}>
                        <div  style={{width:"100%"}}>
                          <TextField 
                            id="cep_client"
                            type="number"
                            label="CEP"
                            fullWidth={true}
                            size="small"
                            required={true}
                            color="primary"
                            placeholder="Digite seu CEP"
                            value={edit? payEditedCep: payNewCep}
                            onChange={
                              edit? (e)=>setPayEditedCep(e.target.value): (e)=>setPayNewCep(e.target.value)
                            }
                            disabled={usuario?.id_perfil === 3 ? true : false}
                          />
                        </div>
                        <div  style={{width:"100%"}}>
                          <TextField 
                              id="street_client"
                              type="text"
                              label="Rua/Av"
                              fullWidth={true}
                              size="small"
                              required={true}
                              color="primary"
                              placeholder="Digite sua rua ou avenida"
                              value={edit? payEditedStreet: payNewStreet}
                              onChange={
                                edit? (e)=>setPayEditedStreet(e.target.value): (e)=>setPayEditedStreet(e.target.value)
                              }
                              disabled={usuario?.id_perfil === 3 ? true : false}
                          />
                        </div>
                        <div style={{width:"100%"}}>
                          <TextField 
                            id="number_client"
                            type="number"
                            label="Número"
                            fullWidth={true}
                            size="small"
                            required={true}
                            color="primary"
                            placeholder="Digite o número"
                            value={edit? payEditedNumberAddress: payNewNumberAddress}
                            onChange={
                              edit? (e)=>setPayEditedNumberAddress(e.target.value): (e)=>setPayNewNumberAddress(e.target.value)
                            }
                            disabled={usuario?.id_perfil === 3 ? true : false}
                          />
                        </div>
                    </div>

                    <div style={{display: "flex", justifyContent: "space-between", gridGap: "1rem", marginTop: '1rem'}}>
                        <div style={{width: '100%'}}>
                          <TextField 
                            id="country_client"
                            type="text"
                            label="Cidade"
                            fullWidth={true}
                            size="small"
                            required={true}
                            color="primary"
                            placeholder="Digite sua cidade"
                            value={edit? payEditedCity: payNewCity}
                            onChange={
                              edit? (e)=>setPayEditedCity(e.target.value): (e)=>setPayNewCity(e.target.value)
                            }
                            disabled={usuario?.id_perfil === 3 ? true : false}
                          />
                        </div>

                        <div style={{width: '100%'}}>
                          <TextField 
                              id="state_client"
                              type="text"
                              label="Estado"
                              fullWidth={true}
                              size="small"
                              required={true}
                              color="primary"
                              placeholder="Digite seu estado"
                              value={edit? payEditedState: payNewState}
                              onChange={
                                edit? (e)=>setPayEditedState(e.target.value): (e)=>setPayNewState(e.target.value)
                              }
                              disabled={usuario?.id_perfil === 3 ? true : false}
                          />
                        </div>

                        <div style={{width: '100%'}}>
                          <TextField 
                              id="neighboour_client"
                              type="text"
                              label="Bairro"
                              fullWidth={true}
                              size="small"
                              required={true}
                              color="primary"
                              placeholder="Digite seu bairro"
                              value={edit? payEditedNeighbourhood: payNewNeighbourhood}
                              onChange={
                                edit? (e)=>setPayEditedNeighbourhood(e.target.value): (e)=>setPayNewNeighbourhood(e.target.value)
                              }
                              disabled={usuario?.id_perfil === 3 ? true : false}
                          />
                        </div>
                        <div style={{width: '100%'}}>
                          <TextField 
                            id="complement_client"
                            type="text"
                            label="Complemento"
                            fullWidth={true}
                            size="small"
                            color="primary"
                            placeholder="Digite um complemento"
                            value={edit? payEditedComplement: payNewComplement}
                            onChange={
                              edit? (e)=>setPayEditedComplement(e.target.value): (e)=>setPayNewComplement(e.target.value)
                            }
                            disabled={usuario?.id_perfil === 3 ? true : false}
                          />
                        </div>
                    
                    </div>
                  </div>
                    {usuario?.id_perfil === 3? '':
                      <div style={{ display: 'flex', gridGap: '1rem', width: '20rem', marginTop: '1rem' }}>
                        {edit ? 
                          <LoadingButton 
                            color='primary' 
                            type='submit' 
                            variant='contained' 
                            size='medium'
                            loading={loading?true: false}
                          >
                            Editar
                          </LoadingButton> 
                          :
                          <LoadingButton 
                            color='primary' 
                            type='submit' 
                            variant='contained' 
                            size='medium'
                            loading={loading?true: false}
                          >
                            Cadastrar
                          </LoadingButton>
                        }

                        <LoadingButton 
                          color='warning' 
                          type='submit' 
                          variant='contained' 
                          size='medium'
                          onClick={(e)=>setModal(!openModal)}
                        >
                          Cancelar
                        </LoadingButton>
                      </div>
                    }
              {success && (
                <p style={{color: 'green'}}>{success}</p>
              )}

              {error && (
                <p style={{color: 'red'}}>{error}</p>
              )}

              </form>
          </section>
        </div>
      )}
    </>
  )
}
function then(arg0: (response: any) => void) {
  throw new Error('Function not implemented.');
}

