import { FC, ChangeEvent, useState, useCallback } from 'react';
import { api } from 'src/api/api';
import PropTypes from 'prop-types';
import React from 'react';
import { FaCloudDownloadAlt } from 'react-icons/fa'
import {
  Tooltip,
  FormControl,
  Divider,
  Box,
  Card,
  Checkbox,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material';


import { read, utils, writeFileXLSX } from 'xlsx';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';
import { BsEye } from 'react-icons/bs';
import BulkActions from '../Clients/BulkActions';
import { ClientsModal } from 'src/components/Modals/ClientsModal/ClientsModal';
import { UserContext } from 'src/context/UserContext';
import { useContext } from 'react';
import { IClient } from 'src/models/users';

interface ClientTableProps {
  className: string;
  clients: IClient[];
}


const applyFilters = (
  clients: IClient[],
): IClient[] => {
  return clients.filter((clients) => {
    let matches = true;

    /*if (filters.status && cryptoOrder.status !== filters.status) {
      matches = false;
    }*/

    return matches;
  });
};

const applyPagination = (
  clients: IClient[],
  page: number,
  limit: number
): IClient[] => {
  return clients.slice(page * limit, page * limit + limit);
};

const ClientsTable: FC<ClientTableProps> = ({ clients }) => {
  const [selectedClients, setSelectedClients] = useState<string[]>(
    []
  );
  const selectedBulkActions = selectedClients.length > 0;
  const [page, setPage] = useState<number>(0);
  const [limit, setLimit] = useState<number>(5);

  const exportFile = useCallback(() => {
    const ws = utils.json_to_sheet(clients);
    const wb = utils.book_new();
    utils.book_append_sheet(wb, ws, "Data");
    writeFileXLSX(wb, "Clientes.xlsx");
  }, [clients]);

  const handleSelectAllClients = (
    event: ChangeEvent<HTMLInputElement>
  ): void => {
    setSelectedClients(
      event.target.checked
        ? clients.map((clients) => clients.id)
        : []
    );
  };

  const handleSelectOneClient = (
    event: ChangeEvent<HTMLInputElement>,
    client: string
  ): void => {
    if (!selectedClients.includes(client)) {
      setSelectedClients((prevSelected) => [
        ...prevSelected,
        client
      ]);
    } else {
      setSelectedClients((prevSelected) =>
        prevSelected.filter((id) => id !== client)
      );
    }
  };

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage);
  };

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value));
  };

  const filteredClients= applyFilters(clients);
  const paginatedClients = applyPagination(
    filteredClients,
    page,
    limit
  );
  const selectedSomeClients =
  selectedClients.length > 0 &&
  selectedClients.length < clients.length;

  const selectedAllClients =
  selectedClients.length === clients.length;

  const theme = useTheme();

  const [data, setData] = useState<IClient[]>([]);
  const [idClient, setIdClient] = useState<IClient[]>([]);
  const [modal, setModal] = React.useState<Boolean>(false)

  async function queryClientsById(idClient: any){
    setModal(!modal)
    setData(null)
    const {data} = await api.get(`/usuarios/${idClient}`);
    if(data){
      setData(data);
    }else{
      setData(null);
    }
  }

  const {
    usuario
  } = useContext(UserContext);



  
  //remove admnin and operator
  let filterPaginatedClients = []
  filterPaginatedClients = paginatedClients.filter(clients => 
    (clients.email != "contato@nestrental.com.br" && clients.email != "vendedor@nestrental.com.br")
  );


  return (
    <Card >
      {selectedBulkActions && (
        <Box flex={1} p={2}>
          <BulkActions/>
        </Box>
      )}
      {!selectedBulkActions && (
        <CardHeader
          action={
            <Box width={150}>
              <FormControl fullWidth variant="outlined">
              </FormControl>
              
                <FaCloudDownloadAlt onClick={exportFile} style={{
                textAlign: 'right',
                position: 'relative',
                left: '7rem',
                height: '25px',
                width: '25px',
                cursor: 'pointer'
               }}/>
             
            </Box>
            
            
          }
          title={"Clientes"}
          
        />
      )}
      
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>
                
              </TableCell>
              <TableCell>Cliente</TableCell>
              <TableCell>CNPJ</TableCell>
              <TableCell>Contato</TableCell>
              <TableCell align="right">Ações</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {filterPaginatedClients.map((clients) => {
              const isClientSelected = selectedClients.includes(clients.id);
              return (
                <TableRow
                  hover
                  key={clients.id}
                  selected={isClientSelected}
                >
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isClientSelected}
                      onChange={(event: ChangeEvent<HTMLInputElement>) =>
                        handleSelectOneClient(event, clients.id)
                      }
                      value={isClientSelected}
                    />
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {clients.entidade.razao_social}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {clients.entidade.documento}
                    </Typography>
                  </TableCell>

                  
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {clients.entidade.email}
                    </Typography>
                  </TableCell>

                  {usuario?.id_perfil === 1? 
                  
                  <TableCell align="right">
                    <Tooltip title="Editar cliente" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryClientsById(clients.id)}
                      >
                        <EditTwoToneIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                  :
                  <TableCell align="right">
                    <Tooltip title="Visuzalizar cliente" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryClientsById(clients.id)}
                      >
                        <BsEye style={{width: '20px', height: '20px'}}/>
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                }
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredClients.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>
      {data  && modal &&(
        <ClientsModal 
        openModal={modal}
        setModal={setModal}
        data={data}
        edit={true}
      />
      )}
      
    </Card>
  );
};

ClientsTable.propTypes = {
  clients: PropTypes.array.isRequired
};

ClientsTable.defaultProps = {
  clients: []
};

export default ClientsTable;
